:root {
  color-scheme: dark;
  --blur: 1px;
  --main-accent: hsl(280 100 50);
  --main-accent-light: hsl(280 100 65);
  --main-light0: hsl(0 0 4);
  --main-light1: hsl(0 0 8);
  --main-light2: hsl(0 0 12);
  --main-light3: hsl(0 0 20);
  --main-light4: hsl(0 0 50);
  --main-text: hsl(0 0 95);
  --border-radius: 0.5rem;
  --animation-prop: 0.25s ease-in-out;
}

* {
  font-family: 'Roboto Condensed';
  box-sizing: border-box;
}

*:focus {
  border: none;
  outline: none;
  box-shadow: inset 0 0 0.25rem -1px var(--main-light4);
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-size: 1rem;
  color: var(--main-text);
  background-color: var(--main-light0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: inherit;
  text-decoration: none;
  transition: text-shadow var(--animation-prop);
}

a:hover {
  text-shadow: 0 0 0.25rem currentColor;
}

h1 {
  color: var(--main-accent);
  margin: 0;
  font-size: 5rem;
  line-height: 90%;
  font-weight: 900;
}

h2 {
  color: var(--main-accent);
  margin: 0;
  font-size: 1.25rem;
  line-height: 90%;
  font-weight: 600;
}

.nav {
  padding: 1rem 0;
  background-color: var(--main-light1);
  color: var(--main-text);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 1600px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--main-accent);
  font-size: 1.25rem;
  font-weight: 600;
}

.logo-img {
  height: 2rem;
  aspect-ratio: 1;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-list {
  display: flex;
  gap: 0.5rem;
}

.main {
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.footer {
  margin-bottom: 0;
  padding: 1rem 0;
  background-color: var(--main-light1);
  color: var(--main-light4);
}

.movies {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: minmax(350px, 1fr) auto auto;
  gap: 1rem;
}

.movie-card {
  position: relative;
  display: grid;
  grid-template-rows: subgrid;
  gap: 0;
  grid-row: span 3;
  background-color: var(--main-light2);
  border-radius: var(--border-radius);
  color: #fff;
  transform-style: preserve-3d;
  transition: transform 0.5s linear;
  transform:
    perspective(100rem)
    rotateX(var(--rotateX)) 
    rotateY(var(--rotateY));
  animation: reveal var(--animation-prop);
}

@keyframes reveal {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.movie-card::before {
  border-radius: var(--border-radius);
  content: '';
  position: absolute;
  z-index: -1;
  inset: 0;
  opacity: 0;
  background: var(--main-accent);
  filter: blur(0.15rem);
  transform: translateZ(-1px);
  transition: opacity var(--animation-prop);
}

.movie-card:hover::before {
  opacity: 1;
}

.movie-poster {
  border-radius: var(--border-radius) var(--border-radius) 0 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: top;
     object-position: top;
}

.movie-title {
  display: flex;
  align-items: center;
  height: 100%;
  background: var(--main-light2);
  padding: 1rem;
  align-self: center;
}

.movie-info {
  color: var(--main-light4);
  border-radius: 0 0 var(--border-radius) var(--border-radius);
  background: var(--main-light1);
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  text-transform: capitalize;
}

.preloader-card {
  position: relative;
  height: 450px;
  display: grid;
  grid-template-rows: subgrid;
  grid-row: span 3;
  background: linear-gradient(0, var(--main-light2) 30%, var(--main-light3) 55%, var(--main-light2) 80%);
  background-size: 200% 200%;
  border-radius: var(--border-radius);
  animation: 
    reveal var(--animation-prop),
    preloader-animation 2s ease-in-out infinite;
}

@keyframes preloader-animation {
  100% {
    background-position: -200% -200%;
  }
}

input {
  margin: 0;
  color: var(--main-text);
}

.btn {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  border: none;
  background-color: var(--main-light2);
  color: var(--main-text);
  transition: box-shadow var(--animation-prop);
}

.btn:disabled {
  cursor: not-allowed;
}

.btn::before {
  display: block;
  content: '';
  width: 1.25rem;
  aspect-ratio: 1;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-image: url('./icons/search.svg');
  mask-image: url('./icons/search.svg');
  background-color: var(--main-text);
  transition: var(--animation-prop);
}

.radio-btn {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  color: var(--main-text);
}

input[type="radio"] {
  --radio-width: 1rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  cursor: pointer;
  display: grid;
  place-content: center;
  width: var(--radio-width);
  aspect-ratio: 1;
  border-radius: 50%;
  border: 1px solid var(--main-light2);
}

input[type="radio"],
input[type="search"],
.search-field {
  transition: box-shadow var(--animation-prop);
}

input[type="radio"]:hover,
.search-field:hover {
  box-shadow: 0 0 0.25rem 0 var(--main-accent);
}

input[type="radio"]::before {
  content: '';
  width: calc(var(--radio-width) / 2);
  aspect-ratio: 1;
  border-radius: 50%;
  filter: blur(var(--blur));
  background-color: var(--main-accent);
  scale: 0;
  transition: scale var(--animation-prop);
}

input[type="radio"]:checked::before {
  scale: 1;
}

.search {
  display: grid;
  gap: 1rem;
}

.search-field {
  overflow: hidden;
  display: flex;
  border: 1px solid var(--main-light2);
  border-radius: var(--border-radius);
}

input[type="search"] {
  font-size: 1rem;
  padding: 1rem;
  width: 100%;
  background-color: transparent;
  border: none;
}

input[type="search"]::-webkit-input-placeholder {
  color: var(--main-light4);
  font-size: 1rem;
}

input[type=search]::-ms-clear,
input[type=search]::-ms-reveal { 
  display: none; 
  width : 0; 
  height: 0; 
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { 
  cursor: pointer;
  appearance: none;
  width: 1rem;
  aspect-ratio: 1;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-image: url('./icons/xmark.svg');
  mask-image: url('./icons/xmark.svg');
  background-color: var(--main-accent);
  transition: var(--animation-prop);
}

.search-filter {
  display: flex;
  gap: 1rem;
}

.error {
  display: flex;
  gap: 0.5rem;
  flex-direction: column;
  align-items: center;
  grid-column: 1 / -1;
  margin: auto auto;
}

.error-title {
  background: linear-gradient(0, var(--main-accent) 30%, var(--main-accent-light) 55%, var(--main-accent) 80%);
  background-clip: text;
  background-size: 200% 200%;
  color: transparent;
  animation: preloader-animation 2s ease-in-out infinite;
}